import { get } from 'lodash';
import { CostSummary } from '../../../mappers/CostSummary';

export class PackageResponse {
  public costSummary: object;

  constructor(rawResponse: object) {
    const priceSummary = get(rawResponse, 'body.priceList[0].priceSummary');
    this.costSummary = CostSummary.mapPriceSummary(priceSummary);
  }
}
